<template>
	<div id="paotui-guide">
		<public-header :title="title" v-if="!isWeixin"></public-header>
		<template v-if="home_setting == 'index' || !home_setting">
			<public-footer :preLoading="showPreLoading" :menufooter="menufooter" :failedTips="failedTips" :showFailedTips="!getLocationStatus"></public-footer>
			<div class="content">
				<div class="map-content">
					<div id="map-container"></div>
				</div>
				<van-swipe class="order-swiper" :autoplay="4000" vertical :show-indicators="false" v-if="orders.length > 0">
					<van-swipe-item v-for="(order, index) in orders" :key="index" >
						<router-link tag="div" :to="util.getUrl({path: '/pages/paotui/diy?id=' + order.order_cid})" class="swiper-item flex-lr">
							<div class="left flex-lr">
								<img :src="order.thumb" alt="">
								<p class="ellipsis">{{order.anonymous_username}}购买了{{order.goods_name}}</p>
							</div>
							<span class="icon icon-right"></span>
						</router-link>
					</van-swipe-item>
				</van-swipe>
				<div class="cate">
					<p class="cate-title">
						平台共有 <span class="c-danger">{{delivery_num}}</span> 位骑士为您服务
					</p>
					<van-row class="cate-list">
						<van-col span="6" v-for="(item, index) in categorys" :key="index">
							<router-link tag="div" :to="util.getUrl({path: '/pages/paotui/diy?id=' + item.id})"  class="cate-item">
								<img :src="item.thumb" alt="">
								<p class="ellipsis">{{item.name}}</p>
							</router-link>
						</van-col>
					</van-row>
				</div>
			</div>
			<template v-if="superRedpacketData.errno == 0 && !superRedpacketData.message.type && superRedpacketData.message.redpackets.length > 0">
				<div class="dialog dialog-redpacket" :class="{'dialog-show': showRedpacket}" >
					<div class="dialog-mask"></div>
					<div class="dialog-container">
						<div class="banner" :style="{'background-color': superRedpacketData.message.page.text.backgroundColor}">
							<img :src="superRedpacketData.message.page.image">
						</div>
						<div class="container" :style="{'background-color': superRedpacketData.message.page.text.backgroundColor}">
							<div class="redpacket" v-for="(item, index) in superRedpacketData.message.redpackets" :key="index">
								<div class="redpacket-info clearfix" :style="{'color': superRedpacketData.message.page.text.color}">
									<div class="col-75 text-left">
										<div class="redpacket-title">{{item.title}}</div>
									</div>
									<div class="col-25 price text-right">{{Lang.dollarSign}}<div>{{item.discount}}</div></div>
								</div>
								<div class="circle-container">
									<div class="circle circle-left"></div>
									<div class="circle circle-right"></div>
								</div>
								<div class="redpacket-limit clearfix flex-lr">
									<div class="use-days-limit">{{item.use_days_limit_text}}</div>
									<div class="use-condition">满{{item.condition}}可用</div>
								</div>
							</div>
							<div class="go-home">
								<div @click="onCloseRedpacket" :style="{'background-color': superRedpacketData.message.page.button.backgroundColor, 'color': superRedpacketData.message.page.button.color}">进入首页</div>
							</div>
							<div class="icon icon-close" @click="onCloseRedpacket"></div>
						</div>
					</div>
				</div>
			</template>
		</template>
		<template  v-else-if="home_setting == 'diy'">
			<template v-for="(homeitem, index) in home.items">
				<template v-if="homeitem.id == 'guide'">
					<public-footer :preLoading="showPreLoading" :menufooter="menufooter" :failedTips="failedTips" :showFailedTips="!getLocationStatus"></public-footer>
					<div class="content" :style="{background: homeitem.style.background}" v-if="">
						<van-swipe :autoplay="3000" class="swiper" indicator-color="#ff2d4b">
							<van-swipe-item v-for="(item, index) in homeitem.picture" :key="index">
								<div @click="util.jsUrl(item.linkurl)" class="img-box">
									<img :src="item.imgurl" alt="">
								</div>
							</van-swipe-item>
						</van-swipe>
						<div class="rider-nearby flex" :style="{color: homeitem.rider.color}">
							<img src="static/img/paotui_rider.png" alt="">
							<span>{{homeitem.rider.text.left}}</span>
							<span class="num" :style="{color: homeitem.rider.num_color}">{{delivery_num}}</span>
							<span>{{homeitem.rider.text.right}}</span>
						</div>
						<div class="goods-edit">
							<div class="goods-input">
								<div class="goods-input-placeholder" v-if="!inputStatus && !note" @click="onChangeInputStatus()">
									<img src="static/img/edit_icon.png" alt="">
									{{homeitem.params.placeholder}}
								</div>
								<div class="goods-input-content flex-lr" v-else>
									<van-field
											v-model="note"
											type="textarea"
											:placeholder="homeitem.params.placeholder"
											class="border-0px"
									/>
									<div class="good-input-submit" @click="onSubmit(homeitem.params.submiturl)">
										<img src="static/img/goods_submit.png" alt="">
									</div>
								</div>
							</div>
							<van-row class="goods-tags" gutter="5">
								<van-col span="6" v-for="(item, index) in homeitem.lanmu" :key="index" >
									<div class="tag-item ellipsis" :style="{color: item.color, 'background-color': item.background_color}" @click="onSubmit(item.linkurl)">
										{{item.text}}
										<div class="tag-item-hot" v-if="item.is_hot == 1">
											<img src="static/img/goods_hot.png" alt="">
										</div>
									</div>
								</van-col>
							</van-row>
						</div>
						<div class="paotui-feature">
							<div class="feature-title">
								跑腿特色
							</div>
							<div class="feature-list">
								<div class="feature-item" v-for="(item, index) in homeitem.feature" :key="index" @click="util.jsUrl(item.linkurl)">
									<div class="feature-item-icon" :style="{'border-color': item.icon_color}">
										<span class="icon" :class="item.icon" :style="{color: item.icon_color}"></span>
									</div>
									<div class="feature-item-text font-13" :style="{color: item.color}">{{item.text}}</div>
								</div>
							</div>
						</div>
					</div>
					<router-link tag="div" :to="util.getUrl({path: '/pages/paotui/order'})" class="paotui-order" style="display:none" :style="{'border-color': homeitem.order.border_color, color: homeitem.order.color}">
						<div class="icon" :class="homeitem.order.icon"></div>
						<span class="font-12">{{homeitem.order.text}}</span>
					</router-link>
				</template>
				<template v-else-if="homeitem.id == 'redpacket'">
					<template v-if="superRedpacketData.errno == 0 && !superRedpacketData.message.type && superRedpacketData.message.redpackets.length > 0">
						<div class="dialog dialog-redpacket" :class="{'dialog-show': showRedpacket}" >
							<div class="dialog-mask"></div>
							<div class="dialog-container">
								<div class="banner" :style="{'background-color': superRedpacketData.message.page.text.backgroundColor}">
									<img :src="superRedpacketData.message.page.image">
								</div>
								<div class="container" :style="{'background-color': superRedpacketData.message.page.text.backgroundColor}">
									<div class="redpacket" v-for="(item, index) in superRedpacketData.message.redpackets" :key="index">
										<div class="redpacket-info clearfix" :style="{'color': superRedpacketData.message.page.text.color}">
											<div class="col-75 text-left">
												<div class="redpacket-title">{{item.title}}</div>
											</div>
											<div class="col-25 price text-right">{{Lang.dollarSign}}<div>{{item.discount}}</div></div>
										</div>
										<div class="circle-container">
											<div class="circle circle-left"></div>
											<div class="circle circle-right"></div>
										</div>
										<div class="redpacket-limit clearfix flex-lr">
											<div class="use-days-limit">{{item.use_days_limit_text}}</div>
											<div class="use-condition">满{{item.condition}}可用</div>
										</div>
									</div>
									<div class="go-home">
										<div @click="onCloseRedpacket" :style="{'background-color': superRedpacketData.message.page.button.backgroundColor, 'color': superRedpacketData.message.page.button.color}">进入首页</div>
									</div>
									<div class="icon icon-close" @click="onCloseRedpacket"></div>
								</div>
							</div>
						</div>
					</template>
				</template>
			</template>
		</template>
	</div>
</template>

<script>
	import {Amap} from '@/config/AMap'
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from "@/components/header"
	import PublicFooter from "@/components/footer"
	export default {
		name: "paotuiGuide",
		components: {
			PublicHeader,
			PublicFooter,
		},
		data() {
			return {
				home_setting: '',
				showPreLoading: true,
				menufooter: {},
				//自定义首页数据
				note: '',
				inputStatus: false,
				title: '跑腿',
				homepage: {},
				home: {},
				//默认首页数据
				categorys: [],
				orders: [],
				delivery_num: 0,
				config: {},
				deliveryers: [],
				superRedpacketData: {
					errno: 0,
					message: {
						page: {},
						redpackets: []
					},
				},
				showRedpacket: false,
				getLocationStatus: true,
				failedTips: {
					type: 'location',
					tips: '获取定位失败!您可以选择手动搜索地址',
					btnText: '手动搜索地址',
					link: '/pages/home/location'
				}
			}
		},
		computed: {
			...mapState([
				'erranderExtra',
			]),
		},
		methods: {
			...mapMutations([
				'setState' 
			]),
			onLoad() {
				this.util.request({
					url: 'errander/guide/index',
					data: {
						forceLocation: 1,
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno  == -1) {
						this.util.$toast(result.message, '', 1000);
						return false;
					} else if(result.errno  == -2) {
						this.getLocationStatus = false;
						this.failedTips.tips = result.message;
						return false;
					}
					result = result.message;
					this.home_setting = result.home_setting;
					if(!this.home_setting || this.home_setting == 'index') {
						this.categorys = [...result.categorys];
						this.orders = [...result.orders];
						this.delivery_num = result.delivery_num;
						this.config = result.config;
						this.deliveryers = result.deliveryers;
						this.superRedpacketData = this.util.extend(this.superRedpacketData, result.superRedpacketData);
						if(this.superRedpacketData.message.redpackets && this.superRedpacketData.message.redpackets.length > 0) {
							this.showRedpacket = true;
						}
						this.menufooter = window.menufooter;
						this.loadmap();
					} else if(this.home_setting == 'diy') {
						this.homepage = result.homepage;
						this.home = this.homepage.data;
						this.delivery_num = this.homepage.delivery_num;
						this.superRedpacketData = this.util.extend(this.superRedpacketData, result.superRedpacketData);
						if(this.superRedpacketData.message.redpackets && this.superRedpacketData.message.redpackets.length > 0) {
							this.showRedpacket = true;
						}
						if(this.home && this.home.page && this.home.page.title) {
							this.title = this.home.page.title;
							this.util.setWXTitle(this.title);
						}
					}
					this.menufooter = window.menufooter;
				}).catch((res) => {
					if(window.forceGetLocationStatus == 'fail') {
						this.showPreLoading = false;
						this.getLocationStatus = false;
					}
				})
			},
			onCloseRedpacket() {
				this.showRedpacket = false;
			},
			onChangeInputStatus() {
				this.inputStatus = true;
			},
			onSubmit(url) {
				this.setState({type: 'erranderExtra', key: 'note', val: this.note});
				this.util.jsUrl(url);
			},
			loadmap(){
				Amap().then((AMap) => {
					var map = new AMap.Map('map-container', {
						resizeEnable: true,
						zoom: 11,
						center: [this.config.map.location_y, this.config.map.location_x],
					});
					for(var i in this.deliveryers) {
						var deliveryer = this.deliveryers[i];
						if(deliveryer.location_x && deliveryer.location_y) {
							var marker = new AMap.Marker({
								position: [deliveryer.location_y, deliveryer.location_x],
								offset: new AMap.Pixel(-26, -80),
								content: '<div class="map-center"><img src="' + deliveryer.avatar + '"alt=""></div>'
							});
							marker.setMap(map);
						}
					}
					map.setFitView();
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	/*默认首页样式*/
	#paotui-guide .map-content{
		width: 100%;
		height: 100%;
		position: relative;
	}
	#paotui-guide .map-content #map-container{
		width: 100%;
		height: 100%;
	}
	#paotui-guide .map-content .map-center{
		position: relative;
		width: 52px;
		height: 80px;
		background: url(../../../static/img/map-location.png) no-repeat 0 0;
		cursor: pointer;
	}
	#paotui-guide .map-content .map-center img{
		position: absolute;
		top: 5px;
		right: 5px;
		width: 42px;
		height: 42px;
		border-radius: 100%;
	}
	#paotui-guide .order-swiper{
		position: absolute;
		top: 10px;
		left: 15px;
		right: 15px;
		z-index: 10;
		background: rgba(0,0,0,.6);
		border-radius: 60px;
		height: 40px;
		line-height: 40px;
		overflow: hidden;
		color: #fff;
		font-size: 14px;
		box-sizing: border-box;
	}
	#paotui-guide .order-swiper .swiper-item{
		padding: 0 10px;
		width: 100%;
		height: 100%;
	}
	#paotui-guide .order-swiper .swiper-item .left{
		width: 92%;
	}
	#paotui-guide .order-swiper .swiper-item .left p{
		width: 87%;
	}
	#paotui-guide .order-swiper .swiper-item img{
		width: 30px;
		height: 30px;
	}
	#paotui-guide .cate{
		position: absolute;
		bottom: 10px;
		left: 10px;
		right: 10px;
		background: rgba(255,255,255,.9);
		border-radius: 5px;
		font-size: 12px;
		z-index: 10;
	}
	#paotui-guide .cate .cate-title{
		margin: 10px 0;
		text-align: center;
	}
	#paotui-guide .cate .cate-item{
		width: 100%;
		color: #333;
		text-align: center;
		margin-bottom: 10px;
	}
	#paotui-guide .cate .cate-item p{
		width: 100%;
		padding: 0 5px;
		margin-top: 5px;
	}
	#paotui-guide .cate .cate-item img{
		width: 45px;
		height: 45px;
	}
	/*自定义页面样式*/
	#paotui-guide .content{
		background: -moz-linear-gradient(top, #f5f5f5 0%, #ffffff 100%);
		background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#f5f5f5), color-stop(100%,#ffffff));
		background: -webkit-linear-gradient(top, #f5f5f5 0%,#ffffff 100%);
		background: -o-linear-gradient(top, #f5f5f5 0%,#ffffff 100%);
		background: -ms-linear-gradient(top, #f5f5f5 0%,#ffffff 100%);
		background: linear-gradient(to bottom, #f5f5f5 0%,#ffffff 100%);
	}
	#paotui-guide .content .swiper{
		width: 100%;
		height: 230px;
	}
	#paotui-guide .content .swiper .img-box{
		width: 100%;
		height: 100%;
	}
	#paotui-guide .content .swiper img{
		width: 100%;
		height: 100%;
	}
	#paotui-guide .content .rider-nearby{
		position: absolute;
		z-index: 100;
		top: 115px;
		left: 50%;
		transform:translate3d(-50%,0,0);
		width:150px;
		height:25px;
		background-color:#623c15;
		border-radius:55px;
		line-height:18px;
		white-space:nowrap;
		padding:0 11px;
		color:#fff;
		font-size:13px;
		text-align: center;
	}
	#paotui-guide .content .rider-nearby img{
		width: 20px;
		height: 17px;
		margin-right: 5px;
	}
	#paotui-guide .content .rider-nearby .num{
		padding: 0 5px;
		color: #f6ce00;
	}
	#paotui-guide .content .goods-edit{
		position:absolute;
		top: 147px;
		left:50%;
		transform:translate3d(-50%,0,0);
		width:93%;
		min-height:230px;
		background:#fff;
		box-shadow:0 0 56px 0 rgba(0,0,0,.14);
		padding:17px 10px 4px;
		z-index:100;
	}
	#paotui-guide .content .goods-edit .goods-input{
		position:relative;
		padding:0 6px;
		height: 100px;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-placeholder{
		width:100%;
		height: 100%;
		line-height:1;
		color:#999;
		font-size:18px;
		display: flex;
		align-items:flex-start;
		line-height: 1.5;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-placeholder img{
		width:16px;
		height:18px;
		margin-right:7px;
		margin-top: 5px;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content{
		height: 100%;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content .van-cell{
		width: 75%;
		height: 100%;
		padding: 0;
		font-size: 18px;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content .van-cell .van-field__body{
		width: 100%;
		height: 100%;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content .van-cell .van-field__body textarea{
		width: 100%;
		height: 100%;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content .good-input-submit{
		width: 25%;
		height: 100%;
	}
	#paotui-guide .content .goods-edit .goods-input .goods-input-content .good-input-submit img{
		width: 73px;
		height: 73px;
		display: block;
		margin: 20px auto 0;
	}
	#paotui-guide .content .goods-edit .goods-tags{
		margin-top: 11px;
	}
	#paotui-guide .content .goods-edit .goods-tags .tag-item{
		position: relative;
		display: inline-block;
		width:100%;
		box-sizing:border-box;
		padding:0 6px;
		height:44px;
		line-height:44px;
		margin-bottom: 5px;
		background: #fef7df;
		font-size: 15px;
		color: #83490d;
		text-align: center;
	}
	#paotui-guide .content .goods-edit .goods-tags .tag-item .tag-item-hot{
		position: absolute;
		left: 0;
		top: 0;
		width:43px;
		height:25px;
	}
	#paotui-guide .content .goods-edit .goods-tags .tag-item .tag-item-hot img{
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	#paotui-guide .content .paotui-feature{
		padding: 20px 0;
		margin-top: 220px;
	}
	#paotui-guide .content .paotui-feature .feature-title{
		text-align:center;
		font-size:15px;
		font-weight:700;
		color:#666;
		margin-bottom:19px;
	}
	#paotui-guide .content .paotui-feature .feature-list{
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#paotui-guide .content .paotui-feature .feature-list .feature-item{
		margin-right: 64px;
		display:flex;
		flex-direction:column;
		align-items:center;
	}
	#paotui-guide .content .paotui-feature .feature-list .feature-item:last-child{
		margin-right: 0;
	}
	#paotui-guide .content .paotui-feature .feature-list .feature-item .feature-item-icon{
		width: 45px;
		height: 45px;
		border-radius: 50%;
		line-height: 45px;
		text-align: center;
		border: 1px solid #FFD56F;
		color: #FFD56F;
		margin-bottom:10px;
	}
	#paotui-guide .content .paotui-feature .feature-list .feature-item .feature-item-icon .icon{
		font-size: 28px;
	}
	#paotui-guide .paotui-order{
		position: fixed;
		z-index: 1000000;
		right: 10px;
		bottom: 160px;
		display:flex;
		flex-direction:column;
		align-items:center;
		width:50px;
		height:50px;
		border-radius:50px;
		text-align:center;
		border:1px solid #FFD56F;
		background:#fff;
		color: #FFD56F;
	}
	#paotui-guide .paotui-order .icon{
		font-size: 24px;
		margin-top: 6px;
	}
	/* 弹出层 */
	#paotui-guide .dialog{
		position: fixed;
		z-index: 100000;
	}
	#paotui-guide .dialog .dialog-mask{
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 100000;
		background: rgba(0, 0, 0, 0.7);
	}
	#paotui-guide .dialog.dialog-show .dialog-mask{
		display: block;
	}
	#paotui-guide .dialog .dialog-container{
		position: fixed;
		top: 0;
		width: 100%;
		background: white;
		transform: translateY(-150%);
		transition: all 0.4s ease;
		z-index: 120;
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog-show .dialog-container{
		transform: translateY(0);
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog .dialog-container .wui-cell::after{
		border-top: 1px solid #e5e5e5;
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog .dialog-container .multiple{
		height: 260px;
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog .dialog-container .multiple scroll-view{
		height: 100%;
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog .dialog-container .multiple .wui-cell::after{
		left: 0;
		right: 0;
	}
	#paotui-guide  #paotui-guide #paotui-guide .dialog .dialog-container .multiple .wui-cell:last-child::after{
		border-top: 0;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .multiple .wui-cell.active{
		color: #FFD161;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .multiple .wui-cell.active .weui-cell__ft{
		display: block;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .multiple .weui-cell__ft{
		display: none;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .multiple .wui-cell-bd .icon{
		margin-right: 5px;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .filter{
		height: 370px;
	}
	#paotui-guide #paotui-guide .dialog .dialog-container .filter scroll-view{
		height: 100%;
	}
	#paotui-guide .dialog .dialog-container .bottom-tab{
		position: relative;
	}
	#paotui-guide .dialog .dialog-container .bottom-tab .clear{
		position: absolute;
		top: 50%;
		left: 15px;
		transform: translate(0,-50%);
		font-size: 12px;
		color: #848484;
	}
	#paotui-guide .dialog .dialog-container .bottom-tab .finish{
		float: right;
		text-align: center;
		background-color: #FFD160;
		padding: 14px 0;
		font-size: 14px;
		width: 100px;
	}
	#paotui-guide .dialog .dialog-container .bottom-tab .finish span{
		margin-left: 5px;
		padding: 2px 5px;
		border-radius: 100%;
		font-size: 10px;
		background-color: #333;
		color: white;
	}
	#paotui-guide .dialog .dialog-container .filter .button-list{
		margin: 15px 15px 0 15px;
	}
	#paotui-guide .dialog .dialog-container .filter .button-list .item{
		font-size: 14px;
		display: inline-block;
		width: 30%;
		text-align: center;
		border: 1px solid #F1EFF0;
		height: 35px;
		line-height: 35px;
		border-radius: 30px;
		margin-bottom: 15px;
		margin-right: 3.3%;
	}
	#paotui-guide .dialog .dialog-container .filter .button-list .item:nth-child(3n+0){
		margin-right: 0;
	}
	#paotui-guide .dialog .dialog-container .filter .button-list .item.active{
		background-color: #FEFBF1;
		color: #FFD160;
		border-color: #FFD160;
	}
	#paotui-guide .dialog .dialog-container .filter .button-list .item .icon{
		font-size: 17px;
		margin-right: 5px;
		color: #FFD160;
	}
	#paotui-guide .dialog .dialog-container .filter .list-title{
		font-size: 14px;
		background-color: #FBFBFB;
		position: relative;
		height: 35px;
		line-height: 35px;
		padding-left: 15px;
	}
	#paotui-guide .dialog .dialog-container .filter .list-title .line{
		height: 13px;
		width: 3px;
		background-color: #FFD160;
		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0,-50%);
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .icon-b{
		width: 17px;
		height: 17px;
		margin-top: 3px;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .wui-cell::after{
		right: 0;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .wui-cell:last-child::after{
		border-top: 0;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .wui-cell .weui-cell__ft{
		display: none;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .wui-cell.active .weui-cell__ft{
		display: block;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .wui-cell.active{
		color: #FFD160;
	}
	#paotui-guide .dialog .dialog-container .filter .active-list .tags::before{
		padding: 2px 4px;
	}
	/* 超级红包样式 */
	#paotui-guide .dialog.dialog-redpacket .dialog-container{
		display: block;
		width:84%;
		margin:0 auto;
		left: 0;
		right: 0;
		position: fixed;
		text-align: center;
		overflow: visible;
		z-index: 100001;
		border-radius: 10px;
		background-color: #b80404;
	}
	#paotui-guide .dialog-show.dialog-redpacket .dialog-container{
		transform: translateY(30%);
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .banner{
		background-color: #b80404;
		height: 76px;
		margin: 0 auto;
		position: relative;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .banner img{
		width: 100%;
		min-height: 80px;
		max-height: 100px;
		position: absolute;
		bottom: 0;
		left: 0;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .container{
		margin: 0 auto;
		padding: 5px 15px 10px;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket{
		background-color: #fff;
		margin-bottom: 12px;
		border-radius: 6px;
		padding: 8px 16px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .redpacket-info{
		height: 26px;
		line-height: 26px;
		clear: both;
		width: 100%;
		color: #fb584f;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .redpacket-info .col-75{
		font-size: 18px;
		float: left;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .redpacket-info .redpacket-title{
		height: 32px;
		line-height: 32px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .price{
		width: 25%;
		float: left;
		text-align: right;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .price div{
		display:inline-block;
		font-size: 26px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .circle-container{
		position: relative;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .circle-container .circle{
		background-color: #b80404;
		width: 16px;
		height: 16px;
		position: absolute;
		border-radius: 16px;
		top: -8px;
		z-index: 20;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .circle-container .circle-left{
		left: -24px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket .circle-container .circle-right{
		right: -24px;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket-limit{
		height: 24px;
		line-height: 24px;
		font-size: 13px;
		color: #666;
		width: 100%;
		margin: 0 auto;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .redpacket-limit>div{
		float: left;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .go-home div{
		padding: 12px 4px;
		border-radius: 4px;
		font-size: 16px;
		max-width: 50%;
		margin: 0 auto;
		background-color: #ffd161;
		color: #333;
		text-align: center;
	}
	#paotui-guide .dialog.dialog-redpacket .dialog-container .icon-close{
		position: absolute;
		left: 50%;
		transform: translate(-50%,0);
		display: inline-block;
		font-size: 20px;
		width: 36px;
		height: 36px;
		text-align: center;
		line-height: 40px;
		color: #fff;
		background: rgba(0,0,0,.7);
		border-radius: 100%;
		margin-top: 16px;
	}
</style>


